.staffer {
  display: flex;
  margin: $sp-4;

  .staffer-image{
    border-radius: 50%;
    height: 100px;
    margin-right: $sp-4;
    transition: opacity 0.3s ease;
  }

  .staffer-image::before {
    content: '';
    background-size: cover;
    background-position: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .staffer-image:hover::after {
    opacity: 1;
  }

  p,
  .staffer-name {
    margin: $sp-1 !important;
  }

  .staffer-pronouns {
    @extend .label, .text-grey-dk-100, .bg-grey-lt-200;
  }

  .staffer-bio {
    font-size: 1em;
  }

  .staffer-office-hours-front {
    font-weight: normal; /* Ensure the whole paragraph is normal by default */
  }
  .office-hours-label {
    font-weight: bold; /* Make only the label bold */
  }
  
}
